













































































$shadow: 0 1px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);

header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 450px;
  .header__inner {
    padding: 0 15px;
    max-width: 800px;
    > .header__name {
      color: rgba(255, 255, 255, .9);
      font-size: 2.5rem;
      text-transform: uppercase;
      padding-bottom: 15px;
      > .header__version {
        position: absolute;
        text-indent: 10px;
        font-size: 1rem;
        vertical-align: top;
        color: #ffa9a9;
      }
    }
    > .header__description {
      color: rgba(255, 255, 255, .7);
      font-size: 1.3rem;
      padding-bottom: 25px;
    }
    > .header__button {
      display: block;
      border: 1px solid #fff;
      background: rgba(255,255,255,.05);
      padding: 12px;
      max-width: 230px;
      width: 100%;
      margin: 0 auto;
      color: #fff;
      font-weight: bold;
      transition: .3s;
      &:hover {
        background: #fff;
        color: #406da5;
      }
    }
  }
}

.section {
  padding-bottom: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  .section__inner {
    max-width: 1600px;
    margin: 0 auto;
    width: 100%;
  }
  .section__title {
    font-size: 1.4rem;
    color: rgba(255, 255, 255, 0.9);
    margin: 0;
    padding: 10px 10px 30px;
    letter-spacing: 1px;
    text-transform: uppercase;
  }
}

/*
 * Works
 */

.list-works {
  display: flex;
  justify-content: space-around;
  flex-wrap: nowrap;
  background: rgba(0, 0, 0, .1);
  box-shadow: $shadow;
  padding: 40px 0;
  > .work {
    color: rgba(255, 255, 255, 0.8);
    display: flex;
    align-items: center;
    justify-content: space-around;
    text-align: center;
    padding: 10px;
    width: 100%;
    &:last-child:after {
      content: none;
    }
    .work__icon {
      text-align: center;
      > i {
        font-size: 2rem;
        margin-bottom: 20px;
      }
    }
    .work__name {
      font-weight: bold;
      font-size: 1.5rem;
      margin-bottom: 10px;
    }
    .work__dot {
      font-size: 1.3rem;
    }
  }
}

/*
 * Benefits
 */

.list-benefits {
  display: flex;
  flex-wrap: wrap;
  > .benefit {
    flex: 1;
    min-width: 31%;
    padding: 30px;
    box-shadow: $shadow;
    background: rgba(0, 0, 0, .1);
    margin: 10px;
    > span {
      display: block;
      margin-bottom: 10px;
      font-size: 1.2rem;
      color: rgba(255, 255, 255, 0.87);
    }
    > p {
      color: rgba(255, 255, 255, 0.7);
    }
    .benefit__title {
      > i {
        margin-right: 5px;
      }
    }
  }
}

/*
 * Action
 */

.section-action {
  max-width: 1000px;
  margin: 0 auto;
  .section__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px;
    background: rgba(0,0,0,.1);
    box-shadow: $shadow;
    > .section__name {
      font-size: 1.4rem;
      color: rgba(255, 255, 255, .87);
      margin-right: 15px;
    }
    > .section__link {
      padding: 10px 30px;
      color: rgba(255, 255, 255, .87);
      border: 1px solid transparent;
      background: rgba(0, 0, 0, .2);
      min-width: 200px;
      text-align: center;
      transition: .2s;
      &:hover {
        background: rgba(0, 0, 0, .3);
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .section {
    padding-bottom: 40px;
    .section__title {
      text-align: center;
    }
  }
  .list-works {
    flex-direction: column;
    .work {
      flex-direction: column;
      .work__dot {
        transform: rotate(90deg);
        margin: 30px 0;
      }
    }
  }
  .header__version {
    display: block;
    position: inherit !important;
    vertical-align: unset !important;
  }
  .list-benefits > .benefit {
    min-width: 100%;
    margin: 10px 0;
  }
  .section-action .section__inner {
    flex-direction: column;
    > .section__name {
      text-align: center;
      margin: 0 0 20px;
    }
  }
}
